/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Is \"Ami\" an Insult?","date":"16 Nov 2007","slug":"/2007-11-16-is-ami-an-insult/","tags":"Y2007"},"html":"<p>At <a href=\"http://martinamr.blogspot.com/\">American im Odenwald</a> Martina wonders why an American in Germany would name a blog <a href=\"http://www.amiexpat.com/\">AmiExpat</a>. <a href=\"http://martinamr.blogspot.com/2007/11/amis.html\">Isn't \"Ami\" an insulting term for Americans?</a></p>\n<p>I've never felt insulted by the term Ami, and said so in her comments. Germans are always coming up with funny shortened names for things, like Wessi, Ossi, Sozi, Fundi. Such terms are more for bar talk than everyday speech, but I've never seen any harm in them. The split in the comments seemed to be 50-50, though, between those who felt uncomfortable with the term and those who did not.</p>\n<p>I'm curious about it, though, so I'll ask me readers here. Is \"Ami\" a harmless term, or have I been being insulted all these years?</p>\n<p>(On the other hand, I don't like the term \"US-amerikanisch\" to describe my nationality or my language. It'd be like referring to \"European Germans\".)</p>"},"next":{"fields":{"title":"Find The Entrance","date":"16 Nov 2007","slug":"/2007-11-16-find-the-entrance/"}},"previous":{"fields":{"title":"Customer Order Display","date":"17 Nov 2007","slug":"/2007-11-17-customer-order-display/"}}},{"node":{"fields":{"title":"Find The Entrance","date":"16 Nov 2007","slug":"/2007-11-16-find-the-entrance/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2037806786\" title=\"View 'CIMG0337' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2385/2037806786_df61018b21.jpg\" alt=\"CIMG0337\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Somewhere behind the big red trash container, the cement mixer and the scaffolding is the main entrance to our restaurant. Yes, we're still open! And we're still running double digit increases in our guest count over last year! Here are some <a href=\"http://www.flickr.com/photos/papascott/sets/72157603204608056/\">more photos</a> of the construction.</p>\n<p>In the meantime we've put our own branding on the container...</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2037053629\" title=\"View 'CIMG0549' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2389/2037053629_b44c0992c6.jpg\" alt=\"CIMG0549\" border=\"0\" width=\"500\" height=\"375\" /></a></p>"},"next":{"fields":{"title":"The Subjunctive Mood is a Sign of Weakness","date":"12 Nov 2007","slug":"/2007-11-12-the-subjunctive-mood-is-a-sign-of-weakness/"}},"previous":{"fields":{"title":"Is \"Ami\" an Insult?","date":"16 Nov 2007","slug":"/2007-11-16-is-ami-an-insult/"}}},{"node":{"fields":{"title":"The Subjunctive Mood is a Sign of Weakness","date":"12 Nov 2007","slug":"/2007-11-12-the-subjunctive-mood-is-a-sign-of-weakness/","tags":"Y2007"},"html":"<p>I've reported before that in Germany you have to <a href=\"http://www.papascott.de/archives/2007/05/16/the-problem-with-no/\">say no very directly</a> for it to be understood as no. I've recently discovered that the same principle can apply to \"yes\".</p>\n<p>Recently in a hotel Frauke mislaid her cell phone and sent me to the reception desk to check if anyone had found it. Indeed, a cell phone had been found, and it looked very similar to Frauke's phone, and I was asked if it was her phone. I couldn't be sure, though, without opening the case, so trying to be friendly and polite I replied \"Yes, it could be.\" <em>(Ja, es k&ouml;nnte sein.)</em></p>\n<p>This was not accepted as an affirmative reply. I forget the exact response, but it was along the lines of \"Well are you sure or are you not?\" or \"Is that supposed to be yes or no?\" I had to start over and say that Frauke's name would appear on the display when it was turned on, and that would prove whether it was her phone or not.</p>\n<p>In Germany it pays to be sure of yourself, even when you're not. Arrogance here is not a negative attribute, but rather a prerequisite. :-)</p>"},"next":{"fields":{"title":"Why Didn't You Park There?","date":"09 Nov 2007","slug":"/2007-11-09-why-didnt-you-park-there/"}},"previous":{"fields":{"title":"Find The Entrance","date":"16 Nov 2007","slug":"/2007-11-16-find-the-entrance/"}}}],"pathPrefix":"","first":false,"last":false,"index":97,"pageCount":1101}}

/***/ })

